.awura-cta-content{
  max-width: 576px;
  margin: 0 auto;
  text-align: center;
  h2{
    margin-bottom: 20px;
  }
  h2, p{
    color: var(--white-color);
  }
}

.awura-cta{
  &-wrap{
    padding: 80px;
    border-radius: 10px;
    background-size: cover;
    background-position: center;
    @include max(991px) {
      padding: 60px 40px;
    }
    @include max(575px) {
      padding: 40px 15px;
    }
  }
  &-content2{
    max-width: 700px;
    margin: 0 auto;
    text-align: center;
    h2{
      margin-bottom: 20px;
      font-size: 85px;
      line-height: .94em;
      letter-spacing: -0.02em;
      font-family: $pixelify-font;
      color: var(--white-color);
      @include max(1399px) {
        font-size: 75px;
      }
      @include max(1199px) {
        font-size: 70px;
        letter-spacing: 0;
      }
      @include max(767px) {
        font-size: 48px;
      }
      @include max(479px) {
        font-size: 40px;
      }
    }
    p{
      font-size: 20px;
      color: var(--white-color);
      font-family: $roboto-font;
      @include max(575px) {
        font-size: 18px;
      }
    }
  
  }
}
