.awura-testimonial{
  &-column{
    gap: 24px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    @include max(991px) {
      gap: 50px;
      grid-template-columns: 1fr;
    }
  }
  &-quote{
    margin-bottom: 30px;
  }
  &-box{
    h3{
      color: var(--white-color);
    }
  }
  &-author{
    &-wrap{
      margin-top: 30px;
      gap: 24px;
      display: flex;
      align-items: center;
    }
    &-thumb{
      width: 60px;
      height: 60px;
      border-radius: 100px;
      overflow: hidden;
      flex-shrink: 0;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-data{
      span{
        font-weight: 500;
        display: block;
        color: var(--white-color);
      }
      p{
        margin: 0;
        color: var(--white-color);
      }
    }
  }
}

.awura-testimonial{
  &-row{
    gap: 24px;
    display: flex;
    margin-bottom: 24px;
    flex-direction: column;
    &.responsive-column{
      @include max(1199px) {
        display: grid;
        gap: 24px;
        grid-template-columns: 1fr 1fr;
      }
      @include max(991px) {
        display: grid;
        gap: 24px;
        grid-template-columns: 1fr;
      }
    }
  }
  &-box2{
    padding: 30px;
    border-radius: 10px;
    background-color: var(--white-color);
    p{
      font-weight: 700;
      color: var(--dark-color-1);
       font-family: $inter-font;
    }
  }
  &-author{
    &-wrap2{
      margin-top: 40px;
      gap: 24px;
      display: flex;
      align-items: center;
    }
    &-thumb2{
      width: 60px;
      height: 60px;
      border-radius: 100px;
      overflow: hidden;
      flex-shrink: 0;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-data2{
      span{
        font-size: 16px;
        font-weight: 600;
        display: block;
        margin-bottom: 5px;
        color: var(--dark-color-1);
        font-family: $inter-font;
      }
      p{
        margin: 0;
        font-size: 16px;
        color: var(--dark-color-1);
        font-family: $inter-font;
      }
    }
  }
}
// End v2

.awura-testimonial{
  &-section{
    padding: 130px 0 270px;
    @include max(991px) {
      padding: 100px 0 240px;
    }
    @include max(767px) {
      padding: 80px 0 200px;
    }
  }
  &-box3{
    padding: 30px;
    border-radius: 10px;
    line-height: 30px;
    background-color: var(--white-color);
    ul{
      margin-bottom: 24px;
      li{
        display: inline-block;
      }
    }
    p{
      font-size: 20px;
      margin-bottom: 24px;
      font-family: $roboto-font;
    }
    span{
      font-size: 16px;
      font-family: $roboto-font;
    }
  }
  &-init{
    .slick-slide{
      margin: 0 12px;
    }
  }
}

.awura-testimonial-next,
.awura-testimonial-prev{
  width: 60px;
  height: 60px;
  display: flex;
  bottom: -140px;
  left: 17%;
  transition: all 0.4s;
  align-items: center;
  border-radius: 10px;
  justify-content: center;
  color: var(--white-color);
  position: absolute;
  background-color: var(--dark-color-2);
  &:hover{
    background-color: var(--blue-color);
  }
  i{
    font-size: 30px;
  }
  @include max(1500px) {
    left: 24px;
  }
  @include max(767px) {
    bottom: -120px;
  }
}
.awura-testimonial-prev{
  left: 21.3%;
  @include max(1500px) {
    left: 100px;
  }
}
// end v3