.awura-video{
  &-section{
    z-index: 0;
    position: relative;
    margin-top: -240px;
    padding-bottom: 130px;
    @include max(991px) {
      padding-bottom: 100px;
    }
    @include max(767px) {
      padding: 80px 0;
      margin: 0;
    }
  }
  &-wrapper{
    position: relative;
    border-radius: 20px;
    overflow: hidden;
    margin: 0 auto;
    transition: width 1.2s ease;
    @include max(767px) {
      border-radius: 10px;
    }
    @include max(767px) {
        height: 250px;
      }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      
    }
 
  }
}

.awura-video-popup{
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
  img{
      @include max(767px) {
        height: 80px;
      }
    }
}

@keyframes waves {
  0% {
    -webkit-transform: scale(0.2, 0.2);
    transform: scale(0.2, 0.2);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
  50% {
    opacity: 0.9;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=90)";
  }
  100% {
    -webkit-transform: scale(0.9, 0.9);
    transform: scale(0.9, 0.9);
    opacity: 0;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  }
}



.waves {
  position: absolute;
  width: 190px;
  height: 190px;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  border-radius: 100%;
  right: -45px;
  bottom: -45px;
  z-index: -1;
   -webkit-animation: waves 3s ease-in-out infinite;
  animation: waves 3s ease-in-out infinite;
  @include max(767px) {
    right: -55px;
    bottom: -55px;
  }
}
        
.wave-1 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}

.wave-2 {
  -webkit-animation-delay: 1s;
  animation-delay: 1s;
}

.wave-3 {
  -webkit-animation-delay: 2s;
  animation-delay: 2s;
}



// v2
.video-card{
    overflow:hidden;
    position:relative;
    border-radius: 20px;
    aspect-ratio: 2 / 2.17;
  }

  /* video element fills card */
  .video-card video{
    width:100%;
    height:100%;
    display:block;
    object-fit:cover;
  }

  /* controls overlay */
  .controls{
    position: absolute;
    left:0;
    right:0;
    bottom:0;
    padding: 30px;
    display:flex;
    flex-direction:column;
    gap: 8px;
    pointer-events:none; /* default off, children enable pointer-events individually */
    background: linear-gradient(0deg, rgba(0,0,0,0.45), rgba(0,0,0,0.12) 40%, transparent 100%);
  }

  .controls-row{
    display:flex;
    align-items:center;
    justify-content:space-between;
    gap:10px;
    pointer-events:auto;
    margin-bottom: 15px;
  }

  /* left group (play + time) */
  .controls-left{
    display:flex;
    align-items:center;
    gap:24px;
  }

  .play-btn{
    display:grid;
    place-items:center;
    cursor:pointer;
  }
  .play-btn svg{ 
    width: 28px; 
    height: 28px; 
    fill:#fff;
   }

  .time{
    color:#fff;
    font-size: 20px;
    font-weight:700;
    line-height: 1;
    color: var(--white-color);
    font-family: $inter-font;
  }

  /* right group: icons */
.controls-right {
    display: flex;
    gap: 24px;
}
  .icon-btn svg{ 
    width: 28px; 
    height: 28px; 
    fill:#fff; 
  }

  /* progress bar */
  .progress-wrap{
    display:flex;
    align-items:center;
    gap:8px;
    pointer-events:auto;
  }

  .progress-wrap input[type="range"]{
    -webkit-appearance:none;
    appearance:none;
    width:100%;
    height: 10px!important;
    border-radius:999px;
    background: linear-gradient(90deg, #ffffff 0%, #ffffff 0%) no-repeat;
    background-size: 0% 100%;
    outline:none;
    cursor:pointer;
    padding: 0!important;
    border: none!important;
    background-color: rgba(255, 255, 255, 0.05);
  }
  /* thumb */
  .progress-wrap input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance:none;
    width:12px;height:12px;border-radius:50%;
    background:white;border:2px solid rgba(0,0,0,0.2);
    box-shadow:0 1px 2px rgba(0,0,0,0.4);
  }
  .progress-wrap input[type="range"]::-moz-range-thumb{
    width:12px;height:12px;border-radius:50%;
    background:white;border:2px solid rgba(0,0,0,0.2);
    box-shadow:0 1px 2px rgba(0,0,0,0.4);
  }

  /* small rounded corners on bottom area to match design */
  .controls{ 
    border-bottom-left-radius: var(--card-radius);
     border-bottom-right-radius: var(--card-radius); }

  /* hover/active emphasis */
  .play-btn:hover, .icon-btn:hover{ transform:translateY(-2px); transition:transform .12s ease; }

