

.blog{
  &-single{
    overflow: hidden;
    margin-bottom: 50px;
    transition: all 0.4s;
    &:hover .blog-img img{
      transform: scale(1.1);
    }
  }
  &-img{
    border-radius: 20px;
    overflow: hidden;
    @include max(767px) {
      border-radius: 10px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
    }
  }
  &-content{
    padding-top: 24px;
  }
  &-meta{
    margin-bottom: 15px;
    display: flex;
    flex-wrap: wrap;
    column-gap: 24px;
    row-gap: 15px;
    a{
      color: var(--primary-color);
      font-size: 16px;
      display: inline-flex;
      font-weight: 600;
      align-items: center;
      transition: all 0.4s;
      &:hover{
        color: var(--accent-color);
      }
      i{
        font-size: 22px;
        margin-right: 15px;
        font-weight: 400;
      }
      
    }
  }
  &-title{
    font-size: 32px;
    line-height: 1.25em; /* 116.667% */
    margin-bottom: 15px;
    transition: all 0.4s;
    @include max(767px) { 
      font-size: 28px;
    }
    a{
      color: var(--primary-color);
      &:hover{
        color: var(--accent-color);
      }
    }
    
  }
  &-titles{
    margin-bottom: 24px;
    font-size: 20px;
    line-height: 26px;
    a{
      color: var(--primary-color);
    }
    
  }
}






.blog-single .blog-text {
  margin-bottom: 28px;
  color: var(--primary-color);
  line-height: 30px;
}

// blog sidebar
.sidebar-area{
  @include max(991px) {
    margin-top: 40px;
  }
}
.widget{
  padding: 30px;
  margin-bottom: 24px;
  border-radius: 10px;
  background-color: var(--gray-color);
  &:last-child{
    margin-bottom: 0;
  }
}
.widget_title{
  font-weight: 500;
  font-size: 32px;
  margin-bottom: 24px;
  line-height: 1.25em;
  &.light-color{
    color: var(--white-color);
    margin-bottom: 15px;
  }
}
.widget select, 
.widget input {
  padding: 15px 24px !important;
  padding-right: 50px !important;
  height: 60px;
  width: 100%;
  transition: all 0.4s;
  border-radius: 50px;
  border: 1px solid var(--accent-color) !important;
  background-color: var(--white-color);
  &:focus{
    border: 1px solid var(--primary-color)!important;
  }
 
}
.widget .search-form{
  position: relative;
}
.widget .search-form button {
  position: absolute;
  right: 5px;
  top: 5px;
  width: 50px;
  height: 50px;
  display: flex;
  border-radius: 100px;
  align-items: center;
  justify-content: center;
  background-color: var(--accent-color);
  color: var(--white-color);
  transition: all 0.4s;
  &:hover{
    background-color: var(--primary-color);
  }
  i{
    font-size: 26px;
  }
}

.widget_nav_menu li, 
.widget_pages li, 
.widget_archive li, 
.widget_categories li {
  display: block;
  position: relative;
  padding: 8px 0;
  &:first-child{
    padding-top: 0;
  }
  &:last-child{
    padding-bottom: 0;
    border-bottom: none;
  }
}

.widget_nav_menu a, 
.widget_pages a, 
.widget_archive a, 
.widget_categories a,
.widget_categories span {
  transition: all .4s;
  color: var(--primary-color);
 
}
.widget_categories span{
  float: right;
}
.widget_nav_menu a:hover, 
.widget_pages a:hover, 
.widget_archive a:hover, 
.widget_categories a:hover {
  color: var(--accent-color);
}


.recent-post {
  display: flex;
  margin-bottom: 20px;
  gap: 15px;
  align-items: center;
  @include max(1399px) {
    flex-direction: column;
  }
  @include max(991px) {
    flex-direction: inherit;
  }
  @include max(479px) {
      flex-direction: column;
  }
  &:hover .media-img img{
    transform: scale(1.1);
  }

  &:last-child{
    margin-bottom: 0;
  }
  .media-img {
    width: 140px;
    height: 140px;
    overflow: hidden;
    border-radius: 10px;
    flex-shrink: 0;
    @include max(1399px) {
      width: 100%;
      height: 200px;
    }
    @include max(991px) {
      width: 140px;
      height: 140px;
    }
    @include max(479px) {
      width: 100%;
      height: 100%;
    }
    img{
      width: 100%;
      height: 100%;
      transition: all 0.3s;
      object-fit: cover;
    }
  }
  .recent-post-meta{
    margin-bottom: 15px;
    a{
      font-size: 16px;
      font-weight: 600;
      color: var(--primary-color);
      display: flex;
      gap: 10px;
      align-items: center;
      i{
        font-size: 22px;
      }
    }
  }
  .post-title {
    color: var(--primary-color);
    display: block;
    margin: 0;
    font-size: 17px;
    a{
      color: var(--primary-color);
    }
  }
}

.recent-post:hover .post-title a {
    color: var(--accent-color);
}

.widget_tag_cloud a, 
.widget .tagcloud a,
.tagcloud a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 15px;
  margin-right: 15px;
  padding: 5px 23px;
  font-size: 16px;
  border-radius: 50px;
  transition: all .4s;
  color: var(--primary-color);
  border: 1px solid rgba(0, 0, 0, 0.5);
  &:hover{
    color: var(--white-color);
    background-color: var(--accent-color);
    border: 1px solid var(--accent-color);
  }
}



// end blog

.blog-details{
  .blog-img{
    margin-bottom: 30px;
  }
  .blog-content{
    padding: 0;
    h3{
      margin-bottom: 15px;
    }
   
  }
  
}

ul.blog-details-list{
 margin-bottom: 24px;
      li{
        margin-bottom: 12px;
        position: relative;
        padding-left: 20px;
        &::before{
          content: "";
          left: 0;
          top: 9px;
          width: 8px;
          height: 8px;
          border-radius: 100px;
          position: absolute;
          background-color: var(--primary-color);
        }
        &:last-child{
          margin-bottom: 0;
        }
        span{
          font-weight: 500;
        }
      }
}


blockquote {
    font-size: 20px;
    line-height: 30px;
    padding: 30px 20px;
    margin: 0 0 30px;
    display: flex;
    border-radius: 10px;
    align-items: flex-start;
    color: var(--primary-color);
    background-color: var(--gray-color);
    border-left: 4px solid var(--accent-color);
}




.share-links {
  padding: 24px 0;
  display: flex;
  align-items: center;
  gap: 15px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  h5{
    font-size: 20px;
  }
  .tagcloud a, 
  .widget .tagcloud a, 
  .widget_tag_cloud a{
    margin-bottom: 0;
  }
}

.social-link{
  &-row{
    display: flex;
    gap: 24px;
    padding: 24px 0;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
     
  }
 
  display: flex;
  gap: 15px;
  align-items: center;
}

.post-btn{
      display: flex;
      font-size: 20px;
      font-weight: 500;
      transition: all 0.4s;
      color: var(--primary-color);
      &:hover{
        color: var(--accent-color);
      }
      i{
        margin-right: 10px;
        font-size: 24px;
      }
}

.blog-inner-title {
  margin-bottom: 30px!important;
}
p.text{
  margin: 0;
}
.comments-wrap {
  padding-top: 24px;
  .post-comment {
    display: flex;
    gap: 24px;
    position: relative;
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .comment-avater{
    width: 60px;
    height: 60px;
    border-radius:100px;
    flex-shrink: 0;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .name{
    font-size: 18px;
  }
  span.commented-on{
    font-size: 16px;
    display: inline-block;
    margin: 0;
  }

  a.replay-btn{
    font-size: 16px;
    font-weight: 500;
    transition: all .4s;
    color: var(--primay-color);
  }
  .children{
    padding-left: 90px;
    @include max(767px) {
      padding-left: 30px;
    }
    
  }
}

.comment-header{
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 10px;
  justify-content: space-between;
}
.comment-header-right {
    display: flex;
    gap: 15px;
    align-items: center;
}
.comment-respond{
  padding: 30px;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  .form-group{
    margin-bottom: 24px;
    label{
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 15px;
    }
    input, 
    textarea{
      margin: 0;
      width: 100%;
      font-weight: 400;
      color: var(--primary-color);
      border-radius: 0;
      background-color: var(--gray-color);
      padding: 15px 24px !important;
      border: none !important;
      transition: all .4s;
      border-radius: 50px;
      border: 1px solid transparent!important;
    }
    textarea{
      height: 200px;
      border-radius: 20px;
      @include max(767px) {
        height: 120px;
      }
    }
    input:focus, 
    textarea:focus{
      border: 1px solid var(--accent-color)!important;
    }
  }
  p{
    margin-top: 15px;
  }
}



button#comment-btn {
  font-size: 16px;
  padding: 5px;
  width: 188px;
  height: 60px;
  border-radius: 6px;
  font-weight: 600;
  display: inline-flex;
  align-items: center;
  border-radius: 50px;
  justify-content: center;
  color: var(--white-color) !important;
  background-color: var(--accent-color);
  gap: 15px;
  transition: all 0.4s;
  &:hover{
    background-color: var(--primary-color);
  }
  i{
    font-size: 26px;
    font-weight: 400;
  }
}
// end blog and blog details
.blog-details{
  .blog-meta  {
    margin-bottom: 24px;

  }
}

// end

