        padding: 24px;
      }
      &:hover{
        .awura-btn-style-two.btn-outline.dark-outline{
          background-color: var(--orange-color);
          border: 1px solid var(--orange-color);
        }
        
      }
    }
    
  &-meta{
    a{
      color: var(--dark-color-1);
      font-size: 18px;
      font-weight: 700;
      font-family: $inter-font;
      position: relative;
      padding-left: 20px;
      &::before{
        content: "";
        left: 0;
        top: 8px;
        width: 10px;
        height: 10px;
        border-radius: 10px;
        position: absolute;
        transition: all 0.4s;
        background-color: var(--dark-color-1);
