  }
  &-title{
    margin-bottom: 24px;
    color: var(--dark-color-1);
    font-family: $anton-font;
    a{
      color: var(--dark-color-1);
      &:hover{
        color: var(--orange-color);
      }
    }
    
  }

  }
}
