// shape v1 hero section
.awura-hero-shape-left {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    clip-path: inset(100% 0 0 0); /* fully hidden from bottom */
    transition: clip-path 1s ease-out;
    img{
        @include max(1399px) {
            width: 250px;
        }
    }
    @include max(767px) {
        display: none;
    }
}

.awura-hero-shape-right {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    clip-path: inset(100% 0 0 0); /* fully hidden from bottom */
    transition: clip-path 1s ease-out;
    img{
        @include max(1399px) {
            width: 250px;
        }
    }
    @include max(767px) {
        display: none;
    }
}

.awura-hero-shape-right.active, 
.awura-hero-shape-left.active {
   clip-path: inset(0 0 0 0); /* fully visible */
}


