.awura-toggle-header {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 30px;
  }

  .toggle-switch {
    position: relative;
    width: 60px;
    height: 30px;
    background: #ccc;
    border-radius: 30px;
    cursor: pointer;
    transition: background 0.3s ease;
  }

  .toggle-switch.active {
    background: #7c3aed;
  }

  .toggle-circle {
    position: absolute;
    top: 4px;
    left: 4px;
    width: 22px;
    height: 22px;
    background: #fff;
    border-radius: 50%;
    transition: all 0.3s ease;
  }

  .toggle-switch.active .toggle-circle {
    left: 34px;
  }

  .awura-pricing-price {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
  }




  .price {
    font-size: 36px;
    font-weight: 700;
    margin: 15px 0;
    transition: all 0.4s ease;
  }
