.awura-integration{
  &-wrap{
    z-index: 0;
    position: relative;
    padding: 100px 0 125px;
    background-size: cover;
    background-position: center;
    background-image: url(../images/v3/integration-shapes.png);
    @include max(991px) {
      padding: 0;
    }
  }
  &-title{
    max-width: 720px;
    margin: 0 auto;
    h2{
       font-size: 85px;
        line-height: .94em;
        letter-spacing: -0.02em;
        font-family: $pixelify-font;
        color: var(--dark-color-2);
        @include max(1399px) {
          font-size: 75px;
        }
        @include max(1199px) {
          font-size: 70px;
          letter-spacing: 0;
        }
        @include max(767px) {
          font-size: 48px;
        }
        @include max(479px) {
          font-size: 40px;
        }
    }
  }
  &-icon{
    z-index: -1;
    border-radius: 8px;
    position: absolute;
    @include max(991px) {
      position: static;
      z-index: 0;
    }
    img{
      @include max(991px) {
        width: 50px;
        @include max(479px) {
          width: 40px;
        }
      }
    }
    &.icon-01{
      top: 0;
      left: 0;
      box-shadow: 0px 3.25px 16.25px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-02{
      top: 0;
      left: 20%;
      box-shadow: 0px 3.15px 15.75px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-03{
      top: 0;
      left: 42%;
      box-shadow: 0px 3.1px 15.5px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-04{
      top: 0;
      right: 15%;
    }
    &.icon-05{
      top: 18%;
      right: 5%;
      box-shadow: 0px 3.1px 15.5px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-06{
      top: 44%;
      left: 0%;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-07{
      top: 35%;
      left: 13%;
    }
    &.icon-08{
      top: 42%;
      right: 14%;
      box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-09{
      bottom: 20%;
      right: 0;
    }
    &.icon-10{
      bottom: 12px;
      left: -3%;
      box-shadow: 0px 3.15px 15.75px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-11{
      bottom: 17%;
      left: 6%;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-12{
      bottom: 0;
      left: 14%;
    }
    &.icon-13{
      bottom: 0;
      left: 40%;
      box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-14{
      bottom: 0;
      right: 35%;
      box-shadow: 0px 3.15px 15.75px 0px rgba(0, 0, 0, 0.15);
    }
    &.icon-15{
      bottom: 0;
      right: 18%;
      box-shadow: 0px 3.15px 15.75px 0px rgba(0, 0, 0, 0.15);
    }
  }
  &-logo{
    @include max(991px) {
      gap: 20px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      margin-top: 40px;
    }
    
  }
}