.awura-chart{
  &-background{
    Width: 526px;
    Height: 520px;
    border-radius: 10px;
    padding: 20px;
    background-image: url(../images/v3/chat-bg.png);
    @include max(991px) {
      margin-bottom: 40px;
      width: 100%;
    }
  }
  &-box{
    padding: 20px;
    border-radius: 10px;
    background-color: var(--white-color);
    h3{
      font-size: 30px;
      margin-bottom: 20px;
      font-family: $pixelify-font;
      color: var(--dark-color-2);
      span{
        color: var(--perfume-color);
      }
    }
  }
  
}

.awura-progress-row{
  display: flex;
  height: 360px;
  justify-content: space-between;
  @include max(479px) {
    height: 300px;
  }
}


.awura-chart {
  display: flex;
  gap: 3px;
  justify-content: space-between;
  align-items: flex-end;
}
.awura-chart.chart-center {
    background: linear-gradient(
    180deg,
    rgba(176, 159, 255, 0.2) 0%,
    rgba(141, 121, 246, 0.2) 100%
  );
    padding: 14px;
    border-radius: 18px;
}

.awura-progress-bar {
  width: 16px;
  opacity: 1;
  border-radius: 3.5px;
  height: 0;
  transition: height 1s ease-in-out;
  @include max(375px) {
    width: 12px;
  }
}
.awura-progress-bar:nth-child(odd) {
   background: linear-gradient(
    180deg,
    rgba(176, 159, 255, 0.4) 0%,
    rgba(141, 121, 246, 0.4) 100%
  );
}

.awura-progress-bar:nth-child(even) {
  background: linear-gradient(180deg, #B09FFF 0%, #8D79F6 100%);
}
.days {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 12px;
  color: #454459;
  line-height: 1;
}