.awura-team{
  &-wrap{
    overflow: hidden;
    border-radius: 20px 20px 0 0;
    transition: all 0.4s;
    margin-bottom: 24px;
    &:hover {
      .awura-team-thumb{
        img{
          transform: scale(1.02);
        }
      }
      .awura-team-data-row{
        background-color: var(--primary-color);
      }
      .awura-team-data{
        h4, p{
          color: var(--white-color);
        }
      }
      .awura-social-icon ul li a{
        color: var(--primary-color);
        background-color: var(--white-color);
      }
    }
  }
  &-thumb{
    display: block;
    overflow: hidden;
    img{
      transition: all 0.5s;
    }
  }
  &-data{
    &-row{
      padding: 24px;
      display: flex;
      border-top: 0;
      align-items: flex-end;
      justify-content: space-between;
      border: 1px solid rgba(0, 0, 0, 0.1);
      transition: all 0.4s;
      border-radius: 0 0 20px 20px;
    }
    h4{
      margin-bottom: 5px;
      transition: all 0.4s;
    }
    p{
      margin: 0;
      transition: all 0.4s        ;
    }
  }
  &-linkedin{
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s;
    color: var(--primary-color);
    border-radius: 100px;
    border: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.awura-social-icon {
  &.team-social{
    ul{
      li{
        a{
          cursor: pointer;
          &:hover{
            background-color: var(--accent-color)!important;
          }
        }
      }
    }
  }
}