.aximo-default-btn {
  font-size: $button_font_size;
  line-height: $buton_line_height;
  padding: $button_padding;
  border-radius: $buton_border_radius;
  font-weight: $button_font_weight;
  font-family: $button_font_family;
  width: fit-content;
  position: relative;
  z-index: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: inline-block;
  text-align: center;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
  overflow: hidden;
  color: var(--light-bg2)!important;
  background-color: var(--orange-bg);
  position: relative;
  z-index: 0;
  overflow: hidden;
  position:relative;
  height: 60px;
  line-height: 59px;
  transform-style: preserve-3d;
  &.disabled,
  &:disabled {
    opacity: 1;
  }
  &.d-block {
    width: 100%;
  }
  &.pill {
    border-radius: 50px;
  }
  &.btn-icon {
    padding: 9px 24px;
  }
  &:focus {
    box-shadow: none;
  }
}


// icon button

.blog{
  &-btn{
    line-height: 22px;
    font-weight: 600;
    display: flex;
    align-items: center;
    transition: all 0.4s;
    color: var(--accent-color);
    i{
      font-weight: normal;
      font-size: 22px;
      margin-left: 8px;
      transition: all 0.4s;
    }
    &:hover{
      color: var(--accent-color);
    }
    .icon-show{
      opacity: 0;
      transition: all 0.4s;
    }
    &:hover .icon-show{
      opacity: 1;
      margin-right: 32px;
    }
    .icon-hide{
      transition: all 0.4s;
      transform: translateX(-28px);
    }
    &:hover .icon-hide{
      opacity: 0;
    }
    span{
      position: relative;
      transform: translateX(-28px);
    }
  }
}

.awura-btn-icon{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    overflow: hidden;
    position: relative;
    font-weight: $button_font_weight;
    gap: 8px;
    text-transform: capitalize;
    color: var(--white-color);
    background-color: var(--accent-color);
    border-radius: $buton_border_radius;
    padding: $button_padding;
    font-size: $button_font_size;
    text-align: left;
    &.white-btn{
      color: var(--primary-color);
      background-color: var(--white-color);
      &:hover{
        color: var(--primary-color);
      }
    }
    &:hover{
      color: var(--white-color);
    }
    
}

.btn-icon{
    position: relative;
    overflow: hidden;
    line-height: 1;
    top: 1px;
}
    .btn-icon{
        transition: transform .38s cubic-bezier(.37,.08,.02,.93),opacity .18s ease-out;
    }
  
    .btn-icon i{
        transition: transform .38s cubic-bezier(.37,.08,.02,.93),opacity .18s ease-out;
        font-size: 22px;
        font-weight: 400;
    }

   .button-icon-hide i{
        position: absolute;
        transform: translateX(-20px);
        top: 0;
   }
   .button-icon-show i{
    display: inline-block;
        transform: translateX(0);
   }
   .awura-btn-icon:hover .button-icon-hide i{
        transform: translateX(0);
   }
   .awura-btn-icon:hover .button-icon-show i{
        transform: translateX(20px);
    }

    .awura-small-btn{
      padding: 11px 25px!important;
    }
    .awura-small-btn2{
      padding: 13px 30px!important;
    }
    .awura-small-btn3{
      padding: 12px 24px!important;
    }
    // button style two

    #awura-submit-btn,
    .awura-btn-style-two{
      display: inline-block;
      font-weight: $button_font_weight;
      text-transform: capitalize;
      color: var(--dark-color-1);
      background-color: var(--orange-color);
      border-radius: $buton_border_radius;
      padding: 18px 35px;
      font-size: $button_font_size;
      line-height: $buton_line_height;
      color: var(--dark-color-1)!important;

      &.btn-outline{
        color: var(--white-color)!important;
        background-color: transparent;
        border: 1px solid rgba(255, 255, 255, 0.5);
        &.dark-outline{
          color: var(--dark-color-1)!important;
          border: 1px solid var(--dark-color-1);
        }
      }
      
  }
.awura-btn-effect-wrap {
    height: 24px;
    position: relative;
    overflow: hidden;
    display: block;
}
.btn-effect-content{
  display: block;
  height: 100%;
  position: relative;
  top: 0;
  transition: .3s;
}

.awura-btn-style-two:hover .btn-effect-content, 
#awura-submit-btn:hover .btn-effect-content {
    top: -100%;
}

// button style three
.awura-header-btn-column{
  display: flex;
  gap: 20px;
  align-items: center;
}
.awura-text-btn{
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
  display: inline-block;
  font-family: $roboto-font;
  color: var(--white-color)!important;
 
}
.awura-btn-position{
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  display: inline-block;
}
.awura-btn-style-three{
  font-size: 16px;
  font-weight: 600;
  line-height: 26px;
  padding: 17px 24px;
  display: inline-block;
  border-radius: 10px;
  transition: all 0.6s;
  font-family: $roboto-font;
  color: var(--white-color)!important;
  background-color: var(--blue-color);
  &:hover{
    color: var(--dark-color-2)!important;
  }
}

.btn-text{
  z-index: 999;
  position: relative;
}

.awura-button-hover{
  background-color: red;
  border-radius: 1000px;
  position: absolute;
  left:0;
  top:0;
  width: 0px;
  height: 0px;
  margin-left: 0px;
  margin-top: 0px;
  pointer-events: none;
  /*animation-timing-function: ease-in-out; */
}

.btn-explode-circle {
   animation: explode 0.5s forwards;

}

.btn-desplode-circle{
   animation: desplode 0.5s forwards;
}

@keyframes explode {
  0% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: var(--marzipan-color);
  }
  100% {
    width: 400px;
    height: 400px;
    margin-left: -200px;
    margin-top: -200px;
    background-color: var(--marzipan-color);
  }
}

@keyframes desplode {
  0% {
    width: 400px;
    height: 400px;
    margin-left: -200px;
    margin-top: -200px;
    background-color: var(--marzipan-color);
  }
  100% {
    width: 0px;
    height: 0px;
    margin-left: 0px;
    margin-top: 0px;
    background-color: var(--marzipan-color);
  }
}