// fonts variable
$primary-font: "Outfit", sans-serif!default;
$anton-font: "Anton", sans-serif!default;
$inter-font: "Inter", sans-serif!default;
$pixelify-font: "Pixelify Sans", sans-serif!default;
$roboto-font: "Roboto", sans-serif!default;
$plus-jakarta-font: "Plus Jakarta Sans", sans-serif!default;
$noto-font: "Noto Serif", serif!default;
$primary-font: "Inter Tight", sans-serif!default;
$onest-font: "Onest", sans-serif!default;
$funnel-font: "Funnel Display", sans-serif!default;
$geist-font: "Geist", sans-serif!default;

// color variable
:root {
  --primary-color: #000000;
  --accent-color: #1248FC;
  --white-color: #fff;
  --pampas-color: #EFECE5;
  --gray-color: rgba(0, 0, 0, 0.05);
  // start v2
  --orange-color: #FFCD61;
  --dark-color-1: #242424;
  // start v3
  --dark-color-2: #26292E;
  --marzipan-color: #F8D597;
  --blue-color: #6083EC;
  --perfume-color: #E5CBF9;
  --gray-color: #F4F6F7;
}

$colors: (
 
);

$bodyfontScale: (
  xl: (
    20: 20px,
    lineheight: 32px,
  ),
  lg: (
    18: 18px,
    lineheight: 28px,
  ),
  md: (
    16: 16px,
    lineheight: 24px,
  ),
  xs: (
    14: 14px,
    lineheight: 22px,
  ),
  xss: (
    12: 12px,
    lineheight: 20px,
  ),
);

// button font size
$button_font_size: 16px;
$buton_line_height: 24px;
$buton_border_radius: 50px;
$button_padding: 16px 26px;
$button_font_weight: 600;
$button_font_family: $primary-font;


