.awura-footer{
  &-top{
    padding: 130px 0 80px;
    @include max(991px) {
      padding: 100px 0 80px;
    }
    @include max(767px) {
      padding: 80px 0 60px;
    }
  }
  &-grid{
    display: grid;
    gap: 155px;
    grid-template-columns: 390px 1fr;
    @include max(1399px) {
      gap: 100px;
    }
    @include max(1199px) {
      gap: 40px;
      grid-template-columns: 1fr;
    }
  }
  &-textarea{
    @include max(1199px) {
      max-width: 600px;
    }
    &.roboto-font{
      font-family: $roboto-font;
    }
    &.light-color{
      color: var(--white-color);
    }
  }
  &-menu{
    &-column{
      display: flex;
      justify-content: space-between;
      @include max(767px) {
        gap: 40px;
        flex-direction: column;
      }
    }
    p{
      font-weight: 500;
      margin-bottom: 24px;
    }
    ul{
      li{
        margin-bottom: 15px;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          color: var(--primary-color);
          transition: all 0.4s;
          &:hover{
            color: var(--accent-color);
          }
        }
      }
    }
    &.contact-info{
      ul{
        li{
          position: relative;
          padding-left: 35px;
          margin-bottom: 24px;
          &:last-child{
            margin-bottom: 0;
          }
          a{
          gap: 10px;
          display: flex;
          align-items: center;
          img{
            position: absolute;
            left: 0;
          }
          }
        }
      }
    }
    &.light-color{
      p{
        color: var(--white-color);
      }
      ul{
        li{
          a{
            color: var(--white-color);
            &:hover{
              color: var(--blue-color);
            }
          }
        }
      }
    }
    &.roboto-font{
      p{
        font-family: $roboto-font;
      }
      ul{
        li{
          a{
            font-family: $roboto-font;
          }
        }
      }
    }
  }
  // End top
  &-bottom{
    display: flex;
    align-items: center;
    padding: 30px 0;
    justify-content: space-between;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    @include max(767px) {
      gap: 24px;
      text-align: center;
      flex-direction: column;
    }
    p{
      margin: 0;
    }
  }
}

// footer start v2
.awura-footer-bottom2 {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 35px 0;
    @include max(767px) {
      gap: 24px;
      text-align: center;
      flex-direction: column;
      justify-content: center;
    }
    p{
      margin: 0;
      font-size: 16px;
      color: var(--dark-color-1);
      font-family: $inter-font;
    }
}
// footer start v3

.awura-footer-bottom3 {
    padding: 35px 0;
    text-align: center;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
    @include max(991px) {
      padding: 25px 0;
    }
    p{
      margin: 0;
      font-size: 16px;
      color: var(--white-color);
      font-family: $roboto-font;
    }
}
