.awura-section-padding{
  padding: 130px 0;
  @include max(991px) {
    padding: 100px 0;
  }
  @include max(991px) {
    padding: 80px 0;
  }
}

.awura-section-padding2{
  padding: 130px 0 106px;
  @include max(991px) {
    padding: 100px 0 76px;
  }
  @include max(991px) {
    padding: 80px 0 56px;
  }
}

.awura-section-pb{
  padding-bottom: 130px;
  @include max(991px) {
    padding-bottom: 100px;
  }
  @include max(991px) {
    padding-bottom: 80px;
  }
}

.awura-section-pb2{
  padding-bottom: 106px;
  @include max(991px) {
    padding-bottom: 76px;
  }
  @include max(991px) {
    padding-bottom: 56px;
  }
}


.awura-section-pt{
  padding-top: 130px;
  @include max(991px) {
    padding-top: 100px;
  }
  @include max(991px) {
    padding-top: 80px;
  }
}

.awura-mt50{
  margin-top: 50px;
  @include max(991px) {
    margin-top: 35px;
  }
}

.awura-p-lg{
  p{
    font-size: 20px;
    line-height: 30px;
    @include max(575px) {
      font-size: 18px;
    }
   }
}

.mb24{
  display: block;
  margin-bottom: 24px;
}

.awura-section-title{
  padding-bottom: 80px;
  max-width: 650px;
  @include max(991px) {
    padding-bottom: 60px;
  }
  @include max(767px) {
    text-align: center;
    padding-bottom: 40px;
  }
  &.center-align{
    max-width: 665px;
    margin: 0 auto;
    text-align: center;
    @include max(991px) {
      max-width: 600px;
    }
    &.max-w-685{
      max-width: 685px;
    }
  
  }
  &.max-w-720{
      max-width: 720px;
    }
  // anton-font
  &.anton-font{
    h5{
      font-size: 24px;
      line-height: 34px;
      margin-bottom: 20px;
      font-family: $anton-font;
      @include max(575px) {
        font-size: 20px;
        margin-bottom: 15px;
      }
    }
    h2{
      font-size: 85px;
      letter-spacing: 0;
      line-height: 1.17em;
      font-family: $anton-font;
      @include max(1399px) {
          font-size: 75px;
        }
        @include max(1199px) {
          font-size: 65px;
        }
        @include max(767px) {
          font-size: 40px;
        }
        @include max(479px) {
          font-size: 36px;
        }
    }
    &.light-color{
      h5, h2{
        color: var(--white-color)!important;
      }
    }
  }
  &.pixelify-font{
     h2{
       font-size: 85px;
      line-height: .94em;
      letter-spacing: -0.02em;
      font-family: $pixelify-font;
      color: var(--dark-color-2);
      @include max(1399px) {
        font-size: 75px;
      }
      @include max(1199px) {
        font-size: 70px;
        letter-spacing: 0;
      }
      @include max(767px) {
        font-size: 48px;
      }
      @include max(479px) {
        font-size: 40px;
      }
     }
  }
}

.awura-default{
  &-thumb{
    @include max(991px) {
      margin-bottom: 40px;
    }
    img{
      width: 100%;
      height: 100%;
      border-radius: 20px;
      object-fit: cover;
      @include max(767px) {
        border-radius: 10px;
      }
    }
  }
  &-content{
    h2{
      margin-bottom: 20px;
    }
    p{
      font-size: 20px;
      @include max(767px) {
        font-size: 18px;
      }
    }
    &.light-color{
      h2, p{
        color: var(--white-color)!important;
      }
    }
  }
}

.pl-75{
  padding-left: 75px;
  @include max(991px) {
    padding-left: 0;
  }
}


.awura-position-sticky{
  top: 100px;
  position: sticky;
  @include max(991px) {
    position: static;
  }
}

.position-relative{
  position: relative;
  z-index: 0;
}

