
   #awura-preloader {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #000;
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 9999;
      transition: opacity 0.6s ease, visibility 0.6s ease;
    }

    #awura-preloader.fade-out {
      opacity: 0;
      visibility: hidden;
    }

    /* Loader Wrapper */
    .awura-loader {
      position: relative;
      width: 120px;
      height: 120px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    /* Rotating Circle Border */
    .awura-loader::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-top-color: rgba(255, 255, 255, 0.7);
      border-radius: 50%;
      animation: rotate 1.5s linear infinite;
    }


    /* Rotation Animation */
    @keyframes rotate {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }