.awura-pagination {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  @include max(991px) {
    margin-top: 60px;
  }
  ul{
    display: flex;
  }
  li {
    display: inline-block;
    margin: 0 2px;
  }
  
}

.awura-pagination span, 
.awura-pagination a,
.pagi-btn {
  height: 40px;
  width: 40px;
  display: inline-flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin: 0 5px;
  color: var(--primary-color);
  border-radius: 50px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  &:hover{
    color: var(--white-color);
    background-color: var(--primary-color);
    border: 1px solid var(--primary-color);
  }
  i{
    font-size: 22px;
  }
}



a.current{
  cursor: not-allowed;
  color: var(--white-color);
  background-color: var(--primary-color);
}

// End pagination