.breadcumb{
  &-wrapper{
    z-index: 0;
    text-align: center;
    padding: 195px 0 80px;
    @include max(991px) {
      padding: 170px 0 80px;
    }
  
    @include max(767px) {
      padding: 130px 0 60px;
    }
   
  }
  &-content{
    text-align: center;
  }

  &-menu{
    ul{
      li{
        line-height: 1;
        z-index: 0;
        padding: 0 5px;
        position: relative;
        display: inline-block;
        font-size: 16px;
        &:first-child{
          padding-left: 0;
        }
        &:last-child{
          padding-right: 0;
        }
        &:before {
          content: "";
          right: -3px;
          top: 3px;
          z-index: -1;
          width: 1px;
          height: 13px;
          position: absolute;
          color: var(--primary-color);
          background-color: var(--primary-color);
          transform: rotate(20deg);
        }
        &:last-child:before{
          content: none;
        }
        a{
          color: var(--heading-color);
        }
      }
    }
  }
  
}

.breadcumb-title {
  margin-bottom: 20px;
}