.awura-portfolio{
  &-column{
    display: grid;
    column-gap: 24px;
    row-gap: 60px;
    grid-template-columns: 1fr 1fr;
    @include max(991px) {
      grid-template-columns: 1fr;
      row-gap: 40px;
    }
  }
  &-wrap{
    border-radius: 20px;
    transition: all 0.4s;
    height: 590px;
    @include max(1399px) {
      height: 520px;
    }
    @include max(1199px) {
      height: 455px;
    }
    @include max(991px) {
      height: 100%;
    }
    &:hover{
      padding: 40px;
      background-color: var(--primary-color);
      @include max(767px) {
        padding: 24px;
      }
    }
    &:hover .awura-portfolio-data p{
      color: var(--white-color);
    }
  }
  &-thumb{
    border-radius: 20px;
    overflow: hidden;
  }
  &-data{
    display: flex;
    margin-top: 24px;
    justify-content: space-between;
    p{
      margin: 0;
      font-weight: 500;
      transition: all 0.4s;
      color: var(--primary-color);
    }
  }
}
// End v1

.awura-portfolio{
  &-wrap2{
    margin-bottom: 24px;
    position: sticky;
    display: block;
    top: 120px;
    transition: all 0.4s ease-in-out;
    &.active{
      transform: scale(.95);
    }
    &:hover .awura-portfolio-thumb2{
      img{
        transform: scale(1.1);
      }
    }
  }
  &-thumb2{
    border-radius: 20px;
    overflow: hidden;
    @include max(991px) {
      border-radius: 10px 10px 0px 0px;
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      transition: all 0.4s;
      @include max(479px) {
        height: 220px;
      }
    }
  }
  &-data2{
    bottom: 30px;
    left: 30px;
    width: 636px;
    padding: 30px;
    border-radius: 10px;
    position: absolute;
    background-color: var(--white-color);
    @include max(991px) {
      position: static;
      width: 100%;
      border-radius: 0 0 10px 10px;
    }
    @include max(479px) {
      padding: 24px;
    }
    h3{
      margin-bottom: 20px;
      font-family: $anton-font;
      color: var(--dark-color-1);
    }
    ul{
      display: flex;
      gap: 24px;
      line-height: 1;
      flex-wrap: wrap;
      li{
        font-weight: 700;
        color: var(--dark-color-1);
        position: relative;
        padding-left: 20px;
        transition: all 0.4s;
        &:before{
          content: "";
          left: 0;
          top: 4px;
          width: 10px;
          height: 10px;
          border-radius: 10px;
          position: absolute;
          background-color: var(--dark-color-1);
        }
      }
    }
  }
}

