  .awura-rolling-counter{
    &-column{
      gap: 100px;
      display: grid;
      max-width: 500px;
      position: relative;
      grid-template-columns: 1fr 1fr;
      @include max(479px) {
        gap: 24px;
        max-width: 250px;
        grid-template-columns: 1fr;
      }
      &:before{
        content: "";
        left: 47%;
        top: 30px;
        width: 1px;
        height: 100px;
        position: absolute;
        background-color: rgba(255, 255, 255, 0.1);
        @include max(479px) {
          content: none;
        }
      }
    }
    &-value{
      margin-bottom: 20px;
      span{
        font-size: 65px;
        font-weight: 500;
        color: var(--accent-color);
      }
    }
  }
  
  .rolling-counter {
    display: inline-flex;
    overflow: hidden;
    font-size: 65px;
    line-height: 70px;
    height: 60px;
    font-weight: 500;
    letter-spacing: -0.02em;
    color: var(--accent-color);
  }

  .rolling-counter .digit {
    display: flex;
    width: 34px;
    flex-direction: column;
    transition: transform 1s ease-out;
  }

  .rolling-counter .digit span {
    height: 60px;
    display: block;
    transition: transform .7s ease-in-out;
  }
// end v1

.awura-rolling-counter{
  &-column2{
    row-gap: 50px;
    column-gap: 30px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    @include max(991px) {
      margin-bottom: 60px;
      display: flex;
      justify-content: space-between;
    }
    @include max(767px) {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
  &-item2{
    .rolling-counter{
      font-size: 85px;
      height: 86px;
      line-height: 1.17em;
      color: var(--orange-color);
      font-family: $anton-font;
      @include max(1199px) {
        font-size: 70px;
      }
      @include max(767px) {
        font-size: 60px;
        height: 70px;
      }
      .digit {
        width: 42px;
        @include max(1199px) {
          width: 35px;
        }
        @include max(767px) {
          width: 30px;
        }
        span{
          height: 87px;
        }
        
      }
    }
    span{
      font-size: 85px;
      height: 95px;
      line-height: 1.17em;
      color: var(--orange-color);
      font-family: $anton-font;
      @include max(1199px) {
        font-size: 70px;
      }
      @include max(767px) {
        font-size: 60px;
      }
    }
    p{
      margin: 0;
      font-size: 20px;
      color: var(--white-color);
      font-family: $anton-font;
    }
  }
  &-value2{
    margin-bottom: 10px;
  }
}
// End v2

