input[type="text"],
input[type="email"],
input[type="password"],
input[type="url"],
input[type="tel"],
input[type="time"],
input[type="date"],
input[type="search"],
input, textarea {
  margin: 0;
  width: 100%;
  height: 60px;
  color:var(--primary-color);
  font-size: 16px;
  border-radius: 50px;
  background-color: transparent;
  padding: 15px 24px !important;
  border: none!important;
  transition: all 0.4s;
  border: 1px solid var(--primary-color)!important;

   &::-webkit-input-placeholder, 
   &::-webkit-input-placeholder{ /* WebKit, Blink, Edge */
      color:    var(--primary-color);
      opacity: 1;
   }
   &:-moz-placeholder, 
   &:-moz-placeholder{ /* Mozilla Firefox 4 to 18 */
      color:    var(--primary-color);
      opacity:  1;
   }
   &::-moz-placeholder, 
   &::-moz-placeholder{ /* Mozilla Firefox 19+ */
      color:    var(--primary-color);
      opacity:  1;
   }
   &:-ms-input-placeholder, 
   &:-ms-input-placeholder{ /* Internet Explorer 10-11 */
      color:    var(--primary-color);
      opacity: 1;
   }
   &::-ms-input-placeholder, 
   &::-ms-input-placeholder{ /* Microsoft Edge */
      color:    var(--primary-color);
      opacity: 1;
   }

   input::placeholder, 
   textarea::placeholder{ /* Most modern browsers support this now. */
      color:    var(--primary-color);
      opacity: 1;
   }
}
input:focus, 
textarea:focus{
  border: 1px solid var(--accent-color)!important;
}
textarea {
  height: 200px;
  resize: none;
  border-radius: 20px;
  @include max(767px) {
    height: 100px;
  }
}


// subscriber footer main
.awura-subscriber{
   position: relative;
   margin-right: 50px;
   input{
      height: 50px;
      padding-right: 50px;
   }
  
}
button#awura-subscriber-btn{
   top: 5px;
   right: 5px;
   width: 40px;
   height: 40px;
   display: flex;
   font-size: 24px;
   border-radius: 50px;
   align-items: center;
   justify-content: center;
   color: var(--white-color);
   position: absolute;
   transition: all 0.4s;
   overflow: hidden;
   background-color: var(--primary-color);
   &:hover{
      background-color: var(--accent-color);
   }
}

.awura-arrow {
  &-show{
    transition: all 0.4s;
  }
  &-hide{
    position: absolute;
    transition: all 0.4s;
    transform: translateX(-35px);
  }
}

button#awura-subscriber-btn:hover .awura-arrow-hide{
   transform: translateX(0);
}
button#awura-subscriber-btn:hover .awura-arrow-show{
   transform: translateX(35px);
}

// subscribe v3 footer

.awura-subscriber2{
   input{
      height: 50px;
      border-radius: 10px;
      font-family: $roboto-font;
      background-color: var(--white-color);
   }
    p{
      margin: 0;
      font-size: 16px;
      margin-top: 15px;
   }
}
button#awura-subscriber-btn2{
   width: 100%;
   height: 50px;
   display: flex;
   font-size: 16px;
   border-radius: 10px;
   align-items: center;
   justify-content: center;
   color: var(--white-color);
   transition: all 0.4s;
   margin-top: 15px;
   background-color: var(--blue-color);
   &:hover{
      color: var(--blue-color);
      background-color: var(--white-color);
   }
}

// blog sidebar subscriber
.widget .sidebar-subscriber {
   position: relative;
   input{
      padding-right: 160px!important;
      background-color: var(--white-color);
      @include max(1399px) {
         padding-right: 24px!important;
      }
      @include max(991px) {
         padding-right: 160px!important;
      }
      @include max(479px) {
         padding-right: 24px!important;
      }
      &:focus{
         border: 1px solid var(--white-color)!important;
      }
   }
}
#sidebar-subscriber-btn{
   top: 5px;
   right: 5px;
   gap: 10px;
   width: 150px;
   height: 50px;
   font-size: 16px;
   display: flex;
   position: absolute;
   align-items: center;
   border-radius: 50px;
   justify-content: center;
   color: var(--white-color);
   transition: all 0.4s;
   background-color: var(--accent-color);
   &:hover{
      background-color: var(--primary-color);
   }
   @include max(1399px) {
      position: static;
      width: 100%;
      margin-top: 15px;
      border: 1px solid var(--white-color);
   }
   @include max(991px) {
      position: absolute;
      width: 150px;
      margin-top: 0;
   }
    @include max(479px) {
      position: static;
      width: 100%;
      margin-top: 15px;
      border: 1px solid var(--white-color);
   }
   i{
      font-size: 24px;
   }
}

// start v2 form

.awura-form-wrap{
   padding: 30px;
   border-radius: 10px;
   background-color: var(--white-color);
}

.awura-input-field{
   margin-bottom: 24px;
   input{
      font-family: $inter-font;
   }
}

