.awura-hero{
  &-section{
    position: relative;
    padding: 195px 0 320px;
    background-color: var(--pampas-color);
     @include max(1199px) {
      padding: 160px 0 320px;
    }
     @include max(767px) {
      padding: 130px 0 80px;
    }
  }
  &-content{
    text-align: center;
    max-width: 1000px;
    margin: 0 auto;
    @include max(1399px) {
      max-width: 830px;
    }
    @include max(1199px) {
      max-width: 700px;
    }
    h1{
      margin-bottom: 20px;
    }
    p{
      max-width: 840px;
      margin: 0 auto;
    }
  }
  // end v1

  &-section2{
    z-index: 0;
    display: flex;
    align-items: center;
    position: relative;
    overflow: hidden;
    padding: 195px 0 150px;
    background-color: var(--accent-color);
    @include max(991px) {
      padding: 170px 0 100px;
    }
    @include max(767px) {
      padding: 130px 0 80px;
    }
  }
  &-content2{
    max-width: 1000px;
    margin: 0 auto;
    text-align: center;
    position: relative;
    @include max(1399px) {
      max-width: 900px;
    }
    @include max(1199px) {
      max-width: 740px;
    }
    h1, h4{
      color: var(--white-color);
      font-family: $anton-font;
    }
    h4{
      margin-bottom: 20px;
    }
    h1{
      font-size: 120px;
      margin-bottom: 24px;
      line-height: 1.08em;
      letter-spacing: 0;
      @include max(1399px) {
        font-size: 100px;
      }
      @include max(1199px) {
        font-size: 80px;
      }
      @include max(991px) {
        font-size: 60px;
      }
      @include max(767px) {
        font-size: 48px;
      }
      @include max(479px) {
        font-size: 40px;
      }
    }
    p{
      font-size: 20px;
      color: var(--white-color);
      font-family: $inter-font;
      @include max(767px) {
        font-size: 18px;
      }
    }
  }
  &-btn {
    display: flex;
    justify-content: center;
    gap: 24px;
    flex-wrap: wrap;
  }
  // end v2
   &-section3{
    z-index: 0;
    padding-top: 80px;
    overflow: hidden;
    background-color: var(--dark-color-2);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    @include max(991px) {
      padding-top: 130px;
    }
  }
  &-content3{
    h4{
      margin-bottom: 20px;
      display: flex;
      gap: 15px;
      font-size: 16px;
      color: var(--marzipan-color);
      font-family: $roboto-font;
    }
    h1{
      font-size: 120px;
      margin-bottom: 24px;
      line-height: 0.95em;
      letter-spacing: -0.02em;
      font-family: $pixelify-font;
      color: var(--white-color);
      font-weight: normal;
      @include max(1399px) {
        font-size: 100px;
      }
      @include max(1199px) {
        font-size: 80px;
      }
      @include max(991px) {
        font-size: 70px;
        letter-spacing: 0;
      }
      @include max(767px) {
        font-size: 48px;
      }
      @include max(479px) {
        font-size: 40px;
      }
    }
    p{
      font-size: 20px;
      color: var(--white-color);
      font-family: $roboto-font;
      @include max(767px) {
        font-size: 18px;
      }
    }
  }
}

.awura-hero-v2-thumb1{
  width: 300px;
  overflow: hidden;
  border-radius: 12px;
  position: absolute;
  transform: rotate(-10deg);
  left: -11%;
  bottom: -53%;
  z-index: -1;
  transition: transform 0.5s ease-out;
  @include max(1399px) {
    max-width: 250px;
  }
 @include max(991px) {
    max-width: 200px;
  }
  @include max(767px) {
    display: none;
  }
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.awura-hero-v2-thumb2{
  width: 300px;
  overflow: hidden;
  border-radius: 12px;
  position: absolute;
  transform: rotate(10deg);
  right: -11%;
  top: -13%;
  z-index: -1;
  transition: transform 0.5s ease-out;
  @include max(1399px) {
    max-width: 250px;
  }
   @include max(991px) {
    max-width: 200px;
  }
  @include max(767px) {
    display: none;
  }
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

// hero v3 card
.awura-hero-card{
  &-wrap{
    height: 845px;
    overflow: hidden;
    padding: 20px;
    background-size: cover;
    background: url(../images/v3/line-bg.png);
    @include max(1199px) {
      height: 700px;
    }
     @include max(991px) {
      margin-top: 40px;
    }
    @include max(575px) {
      height: 500px;
    }
  }
  &-item{
    margin-bottom: 20px;
    img{
      border-radius: 10px;
    }
  }
}