.awura-about-content-box{
  padding: 40px;
  border-radius: 20px;
  background-color: var(--accent-color);
  @include max(1399px) {
    padding: 30px;
  }
  h3{
    margin-bottom: 30px;
    color: var(--white-color);
  }
}

.awura-iconbox{
  &-wrap{
    gap: 30px;
    display: flex;
    position: relative;
    flex-direction: column;
    &::before{
      content: '';
      left: 50%;
      top: 0;
      width: 2px;
      height: 100%;
      position: absolute;
      transform: translateX(-50%);
      background-color: rgba(0, 0, 0, 0.1);
    }
    @include max(991px) {
      margin-top: 40px;
    }
  }
  &-box{
    padding: 28px;
    border-radius: 10px;
    position: relative;
    background-color: var(--white-color);
    &::before{
      content: "";
      left: 50%;
      bottom: -21px;
      width: 12px;
      height: 12px;
      border-radius: 10px;
      position: absolute;
      transform: translateX(-50%);
      background-color: var(--accent-color);
    }
    &:last-child::before{
      content: none;
    }
  }
  &-icon{
    width: 60px;
    height: 60px;
    display: flex;
    margin-bottom: 30px;
    border-radius: 100px;
    align-items: center;
    justify-content: center;
    background-color: var(--pampas-color);
  }
  &-data{
    h3{
      margin-bottom: 15px;
    }
    p{
      margin: 0;
    }
  }
}

.iconbox-sticky{
  top: 85px;
  position: sticky;
  @include max(991px) {
    position: static;
  }
}

// End iconbox v1

.awura-iconbox{
  &-wrapper{
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid rgba(38, 41, 46, 0.1);
    background-color: rgba(38, 41, 46, 0.1);
  }
  &-column2{
    gap: 1px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    @include max(991px) {
      grid-template-columns: 1fr 1fr;
    }
    @include max(767px) {
      grid-template-columns: 1fr;
    }
  }
  &-box2{
    padding: 40px;
    background-color: var(--white-color);
    @include max(1399px) {
      padding: 30px;
    }
  }
  &-icon2{
    margin-bottom: 30px;
  }
  &-data2{
    h3{
      font-size: 32px;
      line-height: 1.18em;
      margin-bottom: 15px;
      letter-spacing: -0.015em;
      font-family: $pixelify-font;
    }
    p{
      font-family: $roboto-font;
    }
  }
}

.awura-text-button{
  color: var(--dark-color-2);
  font-family: $roboto-font;
  &:hover{
    color: var(--blue-color);
  }
}