.awura-text-slider{
  &-section{
    padding: 60px 0;
    overflow: hidden;
    background-color: var(--orange-color);
    @include max(767px) {
      padding: 40px 0;
    }
  }
  display: flex;
  &-item{
    flex-shrink: 0;
    display: flex;
    align-items: center;
    h2{
       font-size: 85px;
      letter-spacing: 0;
      font-family: $anton-font;
      @include max(1399px) {
          font-size: 75px;
        }
        @include max(1199px) {
          font-size: 65px;
        }
        @include max(767px) {
          font-size: 40px;
        }
      
    }
    img{
      margin: 0 50px;
      animation: spin 7s linear infinite;
      @include max(991px) {
        margin: 0 30px;
      }
      @include max(767px) {
        width: 45px;
      }
    }
  }
}


@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}