h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: $primary-font;
  color: var(--primary-color);
  font-weight: 500;
  margin: 0;
}

h1,
.h1 {
  font-size: 100px;
  letter-spacing: -0.03em;
  line-height: 1.12em;
  @include max(1400px) { 
    font-size: 80px;
  }
  @include max(1199px) { 
    font-size: 60px;
    letter-spacing: -0.02em;
  }

  @include max(767px) { 
    font-size: 42px;
    letter-spacing: 0;
  }
}
h2,
.h2 {
  font-size: 65px;
  line-height: 1.07em; /* 116.667% */
  letter-spacing: -0.02em;
  @include max(1199px) { 
    font-size: 60px;
    letter-spacing: -0.01em;
  }
  @include max(991px) { 
    font-size: 50px;
  }
  @include max(767px) { 
    font-size: 40px;
    letter-spacing: 0;
  }
  @include max(575px) { 
    font-size: 36px;
  }
}

h3,
.h3 {
  font-size: 32px;
  line-height: 1.25em; /* 116.667% */
  @include max(767px) { 
    font-size: 28px;
  }
  @include max(479px) { 
    font-size: 24px;
  }
}
h4,
.h4 {
  font-size: 20px;
  line-height: 1.5em; /* 116.667% */
}

p{
  margin-bottom: 30px;
  @include max(767px) {
    margin-bottom: 20px;
  }
}

@for $i from 10 through 40 {
  .f-size-#{$i} {
    font-size: $i * 1px !important;
  }
}

@for $i from 10 through 40 {
  .line-height-#{$i} {
    line-height: $i * 1px !important;
  }
}

.font-bold{
  font-weight: 700;
}
.font-black{
  font-weight: 800;
}
.font-medium{
  font-weight: 500;
}
.font-normal{
  font-weight: 400;
}
.font-semibold{
  font-weight: 600;
}


