.awura-social-icon{
  ul{
    line-height: 1;
    li{
      margin: 0 7px;
      overflow: hidden;
      display: inline-block;
      &:first-child{
        margin-left: 0;
      }
      &:last-child{
        margin-right: 0;
      }
      a{
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50px;
        transition: all 0.4s;
        overflow: hidden;
        position: relative;
        border: 1px solid rgba(0, 0, 0, 0.2);
        i{
          font-size: 18px;
        }
        &:hover{
          background-color: var(--accent-color);
          border: 1px solid var(--accent-color);
        }
        &:hover .awura-social-hide{
          transform: translateY(0);
        }
        &:hover .awura-social-show{
          transform: translateY(-35px);
        }
      }
    }
  }
  &.style-two{
    ul{
      li{
        a{
        border-radius: 5px;
        background-color: var(--white-color);
  
        &:hover{
          background-color: var(--blue-color);
          border: 1px solid var(--blue-color);
        }
     
      }
      }
    }
  }
}


.awura-social {
  &-show{
    transition: all 0.4s;
    i{
      color: var(--primary-color);
    }
  }
  &-hide{
    position: absolute;
    transition: all 0.4s;
    transform: translateY(35px);
    i{
      color: var(--white-color);
    }
  }
}