.awura-brandlogo{
  &-title{
    max-width: 600px;
    padding-bottom: 60px;
  }
  
  &-column{
    gap: 24px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    @include max(1199px) {
      grid-template-columns: repeat(4, 1fr);
    }
    @include max(767px) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include max(479px) {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  &-box{
    border-radius: 10px;
    background-color: var(--pampas-color);
    height: 10px;
    padding: 20px;
    height: 100px;
    width: 100%;
    display: inline-block;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    &:hover{
      .effect-1{
        top: -100%;
      }
    }
  }
}

.awura-brandlogo-link-effect{
  height: 40px;
  position: relative;
  overflow: hidden;
  display: block;
  .effect-1 {
    display: block;
    height: 100%;
    position: relative;
    top: 0%;
    transition: 0.3s;
}
}
// End v1
.awura-brandlogo{
  &-section{
    padding: 80px 0;
    overflow: hidden;
    background-color: var(--dark-color-2);
    @include max(991px) {
      padding: 60px 0;
    }
  }
  &-center-title{
    text-align: center;
    margin-bottom: 50px;
    h5{
      font-size: 24px;
      letter-spacing: -0.01em;
      color: var(--white-color);
      font-family: $pixelify-font;
    }
  }
  &-wrapper{
    position: relative;
  }
  &-slider-row{
    display: flex;
  }
  &-item{
    margin: 0 40px;
    flex-shrink: 0;
  }
  &-overlay{
    position: absolute;
    top: 0;
  }
}