.awura-service{
  &-row{
    position: relative;
    height: 115px;
    padding: 30px 0;
    overflow: hidden;
    transition: all 0.4s ease-in;
    border-bottom: 1px solid var(--dark-color-1);
    @include max(991px) {
      height: 100%!important;
    }
    &:hover .awura-service-thumb{
      transform: scale(1);
      opacity: 1;
    }
     &:hover{
      height: 320px;
      .awura-btn-style-two.btn-outline.dark-outline{
        background-color: var(--orange-color);
        border: 1px solid var(--orange-color);
      }
    }
  }
  &-thumb{
    position: absolute;
    top: 17px;
    right: 300px;
    opacity: 0;
    transition: all 0.8s ease-in;
    transform: scale(.5);
    @include max(1199px) {
      right: 170px;
    }
    @include max(991px) {
      transform: scale(1);
      position: static;
      margin-top: 24px;
      opacity: 1;
    }
    img{
      width: 250px;
      border-radius: 20px;
      @include max(991px) {
        width: 100%;
        border-radius: 10px;
      }
    }
  }
  &-item{
    overflow: hidden;
  }
  &-header{
    display: flex;
    margin-bottom: 15px;
    align-items: center;
    justify-content: space-between;
    h3{
      font-size: 42px;
      line-height: 1.23em;
      font-family: $anton-font;
      color: var(--dark-color-1);
      @include max(991px) {
        font-size: 36px;
      }
      @include max(767px) {
        font-size: 30px;
      }
    }
  }
  &-body{
    max-width: 485px;
    @include max(767px) {
      margin-bottom: 24px;
    }
    p{
      margin-bottom: 50px;
      font-family: $inter-font;
      @include max(991px) {
        margin-bottom: 30px;
      }
    }
  }
  &-list{
    ul{
      column-gap: 24px;
      row-gap: 15px;
      display: flex;
      flex-wrap: wrap;
      li{
        font-weight: 700;
        font-family: $inter-font;
        padding-left: 20px;
        position: relative;
        color: var(--dark-color-1);
        &::before{
          content: "";
          left: 0;
          top: 9px;
          width: 10px;
          height: 10px;
          position: absolute;
          border-radius: 100px;
          background-color: var(--dark-color-1);
        }
      }
    }
  }
}