header.awura-header.main-header{
  background-color: #fff;
}

.awura-header {
  position: fixed;
  z-index: 41;
  width: 100%;
  top: 0;
  transition: all 0.4s;
  @include max(991px) {
    padding: 15px 0;
  }
  
}

// header v1
.awura-header.header-one{
  padding: 15px 0;
  &.sticky-menu{
    background-color: var(--white-color);
  }
  .main-menu{
    border-radius: 50px;
    padding: 0 50px;
    background-color: var(--white-color);
  }
  .menu-style1>ul>li>a {
      padding: 11px 0;
  }
}

// Header v2
.awura-header.header-two{
  &.sticky-menu{
    background-color: var(--accent-color);
  }
  .main-menu {
    a{
      color: var(--white-color);
      &:hover{
        color: var(--orange-color);
      }
    }
  }
}

// Header v3
.awura-header.header-three{
  &.sticky-menu{
    background-color: var(--dark-color-2);
  }
  .header-logo{
    margin-right: 80px;
    @include max(991px) {
      margin-right: 0;
    }
  }
  .main-menu {
    a{
      color: var(--white-color);
      &:hover{
        color: var(--marzipan-color);
      }
    }
  }
}

// header default css

.main-menu {
  a {
    color: var(--primary-color);
    position: relative;
    font-size: 16px;
    display: block;
    transition: all 0.4s;
    &:hover{
      color: var(--accent-color);
    }
  }

  >ul {
    >li {
      margin: 0 14px;
    }
  }


  ul {
    margin: 0;
    padding: 0;

    li {
      list-style-type: none;
      display: inline-block;
      position: relative;
      &.menu-item-has-children {
        >a {
          &:after {
            content: "\ea4e";
            position: relative;
            margin-left: 2px;
            top: 2px;
            font-size: 20px;
            display: inline-block;
            font-family: 'remixicon';
            transition: all .3s ease;
          }
        }
      }
      &:hover{
        &.menu-item-has-children {
          >a {
            &:after {
                      transform: scaleY(-1);
            }
           
          }
          
        }
        
       }

      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        margin-left: 0;
      }

      &:hover {

        >ul.sub-menu {
          visibility: visible;
          opacity: 1;
          margin-top: 0;
          z-index: 9;
        }
      }

    }
  }

  
  ul.sub-menu {
    position: absolute;
    text-align: left;
    top: 100%;
    left: 0;
    background-color: var(--white-color);
    visibility: hidden;
    min-width: 240px;
    width: max-content;
    padding: 7px;
    left: -14px;
    margin-top: 20px;
    opacity: 0;
    z-index: -1;
    border-radius: 10px;
    box-shadow: -2px 2px 70px -25px rgb(0 0 0 / 30%);
    transition: all 300ms linear 0ms;
    transform-origin: 0 0 0;
    li {
      a {
        font-size: 16px;
        line-height: 30px;
        display: inline-block;
        position: relative;
        font-weight: 400;
        text-transform: capitalize;
        display: flex;
        align-items: center;
        &::before {
          content: "";
          height: 2px;
          width: 0;
          display: block;
          background-color: var(--primary-color);
          transition: all 0.3s ease;
        }
        &:hover::before{
          width: 20px;
          margin-right: 10px;
        }
      }
 
      ul.sub-menu {
        transition: all 0.4s;
        margin-left: 0px!important;
        transform: translateY(30px);
      }
      &:hover ul.sub-menu{
        transform: translateY(0px);
      }
    }
    
  }

  .main-menu ul.sub-menu > a .sub-menu-item.menu-item-has-children::before {
    content: "";
    position: absolute;
    bottom: -3px;
    left: auto;
    right: 0;
    height: 0!important;
    width: 0;
    background-color: var(--primary-color);
    transition: all .3s ease;
}


  ul.sub-menu {
    padding: 12px 0;
    left: -27px;

    // &:before {
    //   content: '';
    //   position: absolute;
    //   left: 34px;
    //   top: 30px;
    //   width: 1px;
    //   background-color: var(--primary-color);
    //   height: calc(100% - 65px);
    // }

    li {
      display: block;
      margin: 0 0;
      padding: 4px 10px 4px 20px;
      ul.sub-menu {
        left: 100%;
        right: auto;
        top: 0;
        margin: 0 0;
        margin-left: 20px;

        li {
          ul {
            left: 100%;
            right: auto;
          }
        }
      }
    }
  }





